@import url(https://fonts.googleapis.com/css2?family=Mate&display=swap);
:root {
   --iceblue: rgba(34, 197, 242, 0.35);
   --textcolor: rgba(50, 50, 50, 0.7);
 }

* {
   box-sizing: border-box;
}

body {
   background-color: #FFED99;
   font-family: 'Mate', sans-serif;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   height: 100vh;
   overflow: hidden;
   margin: 0;
   padding: 20px;
}

.App_container__2hsLG {
   -webkit-text-decoration-color: #3B14A7;
           text-decoration-color: #3B14A7;
   background-color: #F6B8B8;
   border-radius: 10px;
   box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
   padding: 50px 20px;
   text-align: center;
   max-width: 100%;
   width: 800px;
}

h3 {
   margin: 0;
   opacity: 0.5;
   letter-spacing: 1px;
}

.App_joke__1t-22 {
   font-size: 30px;
   letter-spacing: 1px;
   line-height: 40px;
   margin: 50px auto;
   max-width: 600px;
}

.App_btn__3EfrB {
   background-color: #AC66CC;
   border: 0;
   border-radius: 10px;
   color: white;
   box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
   padding: 14px 40px;
   font-size: 16px;
   cursor: pointer;
}

.App_btn__3EfrB:active {
   transform: scale(0.98);
}

.App_btn__3EfrB:focus {
   outline: 0;
}

#App_back__1uzty {
   background-color: rgba(34, 197, 242, 0.35);
   background-color: var(--iceblue);
   border: 0.5rem solid rgba(34, 197, 242, 0.35);
   border: 0.5rem solid var(--iceblue);
   border-radius: 25%;
   color: white;
   font-size: 2rem;
   padding: 0.5rem;
   position: absolute;
   top: 0;
   left: 0;
   text-decoration: none;
 }
 
 #App_back__1uzty:hover {
   background-color: white;
   border: 0.5rem solid white;
   color: black;
 }
 
 #App_back__1uzty:active {
   transform: scale(96%);
 }
